.list__container {
	display: flex;
	flex-wrap: wrap;
	margin: 0 0 50px 0;
	padding: 0;
	list-style-type: none;
}

.list__item {
	width: 190px;
	margin: var(--spacing-medium);
	margin-bottom: var(--spacing-large);
	border-radius: var(--border--radius-small);
	background-color: var(--color-white);
	cursor: pointer;
	position: relative;
	left: 0;
	top: 0;
}

.list__item:hover {
	box-shadow: 0 0 8px var(--theme-default-header);
}

.list__item a {
	text-decoration: none;
	color: var(--color-black);
	text-align: center;
}

.person__photo {
	width: 100%;
	height: 260px;
	object-fit: cover;
	object-position: top center;
	border-radius: var(--border--radius-small) var(--border--radius-small) 0 0;
}

.btnRemove {
	position: absolute;
	bottom: -35px;
	right: 0;
	width: 30px;
	height: 30px;
	cursor: pointer;
}

.btnRemove:hover {
	transform: translate(1px, 1px);
}
